.section-home__content {
	height: 100%;
	display: flex;
	align-items: flex-end;
	justify-content: space-between;
}
.h1 {
	font-size: 100px;
	font-weight: bold;
}
.h2 {
	font-size: 70px;
	font-weight: bold;
}
.glitch{
	position:relative;
	margin:0 auto;
}
@keyframes noise-anim{
	$steps:20;
	@for $i from 0 through $steps{
		#{percentage($i*(1/$steps))}{
			clip:rect(random(100)+px,9999px,random(100)+px,0);
		}
	}
}
.glitch:after{
	content:attr(data-text);
	position:absolute;
	left:2px;
	text-shadow:-1px 0 red;
	top:0;
	color:black;
	overflow:hidden;
	clip:rect(0,900px,0,0);
	animation:noise-anim 2s infinite linear alternate-reverse;
}

@keyframes noise-anim-2{
$steps:20;
	@for $i from 0 through $steps{
		#{percentage($i*(1/$steps))}{
			clip:rect(random(100)+px,9999px,random(100)+px,0);
		}
	}
}
.glitch:before{
	content:attr(data-text);
	position:absolute;
	left:-2px;
	text-shadow:1px 0 blue;
	top:0;
	color:black;
	overflow:hidden;
	clip:rect(0,900px,0,0);
	animation:noise-anim-2 3s infinite linear alternate-reverse;
}
$bg: #f3f8fa;
$white: #fff;
$black: #282936;
@mixin transition($property: all, $duration: 0.45s, $ease: cubic-bezier(0.65,0,.076,1)) {
	transition: $property $duration $ease;
}
.section-home__content-link {
	position: relative;
	display: inline-block;
	cursor: pointer;
	outline: none;
	border: 0;
	vertical-align: middle;
	text-decoration: none;
	background: transparent;
	padding: 0;
	font-size: inherit;
	font-family: inherit;
	width: 12rem;
	height: 50px;
	&:focus {
		outline: none;
	}
	.link-circle {
		@include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
		position: relative;
		margin: 0;
		width: 3rem;
		height: 3rem;
		background: $black;
		border-radius: 1.625rem;
		.icon {
			@include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
			position: absolute;
			top: 0;
			bottom: 0;
			margin: auto;
			background: $white;
			&.arrow {
				@include transition(all, 0.45s, cubic-bezier(0.65, 0, .076, 1));
				left: 0.625rem;
				width: 1.125rem;
				height: 0.125rem;
				background: none;

				&::before {
					position: absolute;
					content: '';
					top: -0.25rem;
					right: 0.0625rem;
					width: 0.625rem;
					height: 0.625rem;
					border-top: 0.125rem solid #fff;
					border-right: 0.125rem solid #fff;
					transform: rotate(45deg);
				}
			}
		}
	}
	.link-text {
		@include transition(all, 0.45s, cubic-bezier(0.65,0,.076,1));
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		padding: 0.75rem 0;
		margin: 0 0 0 1.85rem;
		color: $black;
		font-weight: 700;
		line-height: 1.6;
		text-align: center;
		text-transform: uppercase;
	}
	&:hover {
		.link-circle {
			width: 100%;
			.icon {
				&.arrow {
					background: $white;
					transform: translate(1rem, 0);
				}
			}
		}
		.link-text {
			color: $white;
		}
	}
}
@media (max-width: 1199px) {
	.h1 {
		font-size: 80px;
	}
	.h2 {
		font-size: 60px;
	}
}
@media (max-width: 991px) {
	.h1 {
		font-size: 70px;
	}
	.h2 {
		font-size: 55px;
	}
}
@media (max-width: 767px) {
	.h1 {
		font-size: 45px;
	}
	.h2 {
		font-size: 40px;
	}
	.section-home__content {
		flex-direction: column;
		align-items: flex-start;
	}
	.section-home {
		height: auto;
		padding: 75px 0;
	}
	.section-home__content-title {
		margin-bottom: 15px;
	}
}
@media (max-width: 575px) {
	.h1 {
		font-size: 40px;
	}
	.h2 {
		font-size: 30px;
	}
}
